import React, { Component } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { Panel, PanelBody, PanelHeader } from "../../../components/panel/panel";
import {
  Loading,
  ModalGlobal,
  postData,
  ToastNotification,
  reset,
} from "../../../components/helpers/library";
import {
  getDataKas,
  hideModal,
  showModal,
} from "../../../actions/datamaster_action";
import HeadAmbilUangKas from "../AmbilUangKas/HeadAmbilUangKas";
import HeadTambahKas from "../TambahUangKas/HeadTambahKas";
import { setCamera } from "../../../actions/databarang_action";
import { dataURLtoFile } from "../../../components/helpers/function";
import { getImage, postImage } from "../../../config/axios";
import ModalGambar from "./ModalGambar";
import paginationFactory from "react-bootstrap-table2-paginator";
import BootstrapTable from 'react-bootstrap-table-next';

const maptostate = (state) => {
  return {
    datakas: state.datamaster.getDataKas.datauang,
    count: state.datamaster.getDataKas.count || 0,
  };
};

const RemotePagination = ({ data, page, sizePerPage, onTableChange, totalSize, columns }) => (
  <div>
    <BootstrapTable
      remote
      keyField="_id"
      data={ data }
      columns={ columns }
      pagination={ paginationFactory({ page, sizePerPage, totalSize }) }
      onTableChange={ onTableChange }
    />
  </div>
);
class LihatUangkas extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      page: 1,
      data: [],
      sizePerPage: 10,
      count: 0,
      columns: [
        {
          dataField: "kategori",
          text: "kategori",
          footer: "",
          footerAttrs: {
            colSpan: "2",
          },
        },
        {
          dataField: "keterangan",
          text: "Keterangan",
        },
        // {
        //   dataField: "deskripsi",
        //   text: "Deskripsi",
        // },
        {
          dataField: "jumlah_in",
          text: "Jumlah",
          headerClasses: "text-right",
          formatter: (cell) => {
            return (
              <div className="text-right"> {cell.toLocaleString("kr-KO")} </div>
            );
          },
          footer: (cell) => {
            return (
              <div className="text-right">
                {" "}
                {cell
                  .reduce((a, b) => a + parseInt(b), 0)
                  .toLocaleString("kr-KO")}{" "}
              </div>
            );
          },
        },
        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row) => {
            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    onClick={() => this.lihatgambar(row)}
                    className="btn btn-primary"
                  >
                    <i className="fa fa-eye"></i>
                  </button>
                </div>
              </div>
            );
          },
        },
      ],
    };
  }
  async lihatgambar(row) {
    this.setState({
      isLoading: true,
    });
    await getImage("BUKTI_UANG_KAS/" + row.no_reff)
      .then((res) => {
        this.setState({
          isLoading: false,
        });
        this.props.dispatch(setCamera(res));
        this.showModal("GAMBAR");
      })
      .catch((err) => {
        this.setState({
          isLoading: false,
        });
        ToastNotification(
          "info",
          err.code === 404
            ? "Gambar Tidak Ada"
            : "Gagal Mengambil Gambar SIlahkan Ulangi Kembali"
        );
      });
  }
  componentDidMount() {
    this.props.dispatch(getDataKas(1, 10));
  }

  handleTableChange = (type, { page, sizePerPage }) => {
    const currentIndex = (page - 1) * sizePerPage;
    this.setState({
      isLoading: true,
    });
    this.props.dispatch(getDataKas(currentIndex, sizePerPage));
    this.setState(() => ({
      page,
      sizePerPage,
      isLoading: false,
    }));
  };
  showModal(row) {
    this.setState({
      form: row,
    });
    this.props.dispatch(showModal());
  }
  simpanTambahBarang(data, urldata) {
    this.setState({ isLoading: true });

    let data_post = {
      kategori: data.kategori,
      jumlah_rp: data.jumlah,
      deskripsi: data.keterangan,
    };
    const file = data.foto !== undefined ? dataURLtoFile(data.foto) : undefined;

    let url = urldata ? "cash/in" : "cash/out";
    postData(url, data_post)
      .then((res) => {
        this.props.dispatch(setCamera(false));
        this.setState({ isLoading: false });
        this.props.dispatch(hideModal());
        if (file !== undefined) {
          postImage(file, `BUKTI_UANG_KAS/${res.data.no_reff}`)
            .then((res) => {})
            .catch((err) => {
              ToastNotification(
                "info",
                "upload Foto Gagal, Silahkan Edit Didata Barang"
              );
            });
        }
      })

      .then(() => {
        ToastNotification("success", "Data Berhasil Disimpan");
        this.props.dispatch(getDataKas(1, 10));
      })
      .then(() => {
        this.props.dispatch(reset("HeadTambahKas"));
      })
      .catch((error) => this.ErrorHandling(error));
  }

  ErrorHandling(err) {
    ToastNotification(
      "info",
      err.response?.data?.message || "Gagal Menyimpan Data"
    );
    this.setState({ isLoading: false });
  }
  render() {
    const { sizePerPage, page } = this.state;
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Transaksi Kas</Link>
          </li>
          <li className="breadcrumb-item active">Lihat Uang Kas</li>
        </ol>
        <h1 className="page-header">Lihat Uang Kas </h1>
        <Panel>
          <PanelHeader>Lihat Uang Kas</PanelHeader>
          <PanelBody>
            <div className="col-lg-12 mb-2">
              <div className="row justify-content-end">
                  <button className="btn btn-success" onClick={() => this.showModal("AMBIL")}>Ambil Uang Kas<i className="fa fa-minus ml-3"></i></button>
              <button className="btn btn-primary ml-2" onClick={() => this.showModal("TAMBAH")}>Tambah Uang Kas<i className="fa fa-plus ml-3"></i></button>
              </div>
            </div>
            <RemotePagination
              data={ this.props.datakas || [] }
              columns={this.state.columns}
              page={ page }
              sizePerPage={ sizePerPage }
              totalSize={ this.props.count }
              onTableChange={ this.handleTableChange }
            />
            {/* <Tabel
              tambahData
              btnText="Tambah Uang Kas"
              handleClick={() => this.showModal("TAMBAH")}
              btnOptional
              btnTextOptional="Ambil Uang Kas"
              handleClickOptional={() => this.showModal("AMBIL")}
              iconBtnOptional={
                <>
                  <i className="fa fa-minus ml-3"></i>{" "}
                </>
              }
              data={this.props.datakas}
              onTableChange={this.handleTableChange}
              keyField="_id"
              pagination={ paginationFactory({ page : page, sizePerPage: sizePerPage, totalSize: count }) }
              columns={this.state.columns}
              empty={true}
              textEmpty="Data Uang Kas Kosong"
            /> */}
          </PanelBody>
        </Panel>
        <ModalGlobal
          size="P"
          title={
            this.state.form === "TAMBAH"
              ? "Tambah Uang Kas"
              : this.state.form === "AMBIL"
              ? "Ambil Uang Kas"
              : "BUKTI TRANSAKSI"
          }
          content={
            this.state.form === "TAMBAH" ? (
              <HeadTambahKas
                isLoading={this.state.isLoading}
                onSubmit={(data) => this.simpanTambahBarang(data, true)}
              />
            ) : this.state.form === "AMBIL" ? (
              <HeadAmbilUangKas
                isLoading={this.state.isLoading}
                onSubmit={(data) => this.simpanTambahBarang(data, false)}
              />
            ) : (
              <ModalGambar />
            )
          }
        />
        {this.state.isLoading ? (
          <Loading
            loading
            background="rgba(0, 0, 0, 0.35)"
            loaderColor="rgba(94, 147, 117, 1)"
          />
        ) : null}
      </div>
    );
  }
}

export default connect(maptostate)(LihatUangkas);
