import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { createNumberMask } from "redux-form-input-masks";
import {
  HiidenFiled,
  ReanderField,
  ReanderSelect,
} from "../../../components/helpers/field";
import { getData } from "../../../config/axios";
import ValidasiUangKas from "../../../Validasi/UangKas/ValidasiUangKas";
import Loading from "react-fullscreen-loading";
import {
  imgfound,
  Camera,
  showModal,
} from "../../../components/helpers/library";
import {
  dataURLtoFile,
  resizeFile,
} from "../../../components/helpers/function";
import { setCamera } from "../../../actions/databarang_action";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  suffix: " ,-",
  locale: "kr-KO",
});
const maptostate = (state) => {
  return {
    setCamera: state.databarang.setCamera,
  };
};
class HeadTambahKas extends Component {
  constructor(props) {
    super(props);
    this.state = {
      parametertransaksi: [],
      ModalWebCam: false,
      foto : ""
    };
  }

  componentDidMount() {
    getData("paratransaksi/get/all")
      .then((res) => {
        this.setState({
          parametertransaksi: res.data,
        });
      })
      .catch((err) => {
        console.log(err);
      });
  }
  loadcamera() {
    return (
      <Camera
        onTakePhoto={(dataUri) => {
          this.handleTakePhoto(dataUri);
        }}
      />
    );
  }
  async handleTakePhoto(url) {
    try {
      const file = dataURLtoFile(url);
      const res = await resizeFile(file);
      this.setState({
        foto : res,
        ModalWebCam: false
      })
      this.props.change("foto", res);
      this.props.dispatch(setCamera(true));

    } catch (err) {
      // console.log(err);
    }
  }

  componentWillUnmount(){
    this.props.dispatch(setCamera(false));
  }
  showModalWebcam() {
    this.props.dispatch(setCamera(false));
    this.props.dispatch(showModal());
    this.setState({ ModalWebCam: true });
  }
  getValue = async (event) => {
    try {
      const file = event.target.files[0];
      const res = await resizeFile(file);
      // console.log(res);
      this.setState({
        foto : res,
        ModalWebCam: false
      })
      this.props.dispatch(setCamera(true));
      this.props.change("foto", res);
    } catch (err) {
      // console.log(err);
    }
  };
  render() {
    return (
      <form
        onSubmit={this.props.handleSubmit}
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
      >
        <Field
          readOnly={true}
          name="foto"
          type="hidden"
          component={HiidenFiled}
        />
        <div className="row">
          <div className="col-lg-12 text-center">
            {this.state.ModalWebCam === true ? (
              this.props.setCamera === false ? (
                <div className="container">{this.loadcamera()}</div>
              ) : (
                <>
                  <img
                    src={
                      this.props.setCamera === false
                        ? imgfound
                        : this.state.foto
                    }
                    alt="img"
                    height="245px"
                    width="355px"
                  />
                </>
              )
            ) : (
              <>
                <img
                  src={
                    this.props.setCamera === false
                    ? imgfound
                    : this.state.foto
                  }
                  alt="img"
                  height="245px"
                  width="355px"
                />
              </>
            )}
          </div>
        </div>
        <br />
        <div className="row">
          <div className="col-2"> </div>
          <div className="col-5">
            <div className="input-group">
              <div className="custom-file">
                <input
                  tabIndex="1"
                  type="file"
                  name="photo"
                  autoComplete="off"
                  className="custom-file-input"
                  id="exampleInputFile"
                  onChange={(e) => this.getValue(e)}
                />
                <label className="custom-file-label" htmlFor="exampleInputFile">
                  Pilih gambar
                </label>
              </div>
            </div>
          </div>
          <div className="col-3">
            <button
              tabIndex="2"
              className="btn btn-block btn-primary"
              type="button"
              onClick={() => this.showModalWebcam()}
            >
              {" "}
              WebCam{" "}
            </button>
          </div>
          <hr />
        </div>
        <hr />
        <div className="row">
          <div className="col-lg-4">
            <Field
              name="kategori"
              component={ReanderSelect}
              options={this.state.parametertransaksi.map((list) => {
                let data = {
                  value: list.kode_transaksi,
                  name: list.kode_transaksi,
                };
                return data;
              })}
              label="Kategori"
              placeholder="Masukan Kategori"
            />
          </div>
          <div className="col-lg-4">
            <Field
              name="jumlah"
              component={ReanderField}
              type="telp"
              label="Jumlah"
              placeholder="Masukan Jumlah"
              {...currencyMask}
            />
          </div>
          <div className="col-lg-4">
            <Field
              name="keterangan"
              component={ReanderField}
              type="text"
              label="Keterangan"
              placeholder="Masukan Keterangan"
            />
          </div>
          <div className="col-lg-12">
            <div className="text-right">
              <button className="btn btn-primary">
                {this.props.isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Menyimpan
                  </>
                ) : (
                  <>
                    Simpan <i className="fa fa-paper-plane ml-3"></i>
                  </>
                )}
              </button>
            </div>
          </div>
        </div>
        {this.props.isLoading ? (
          <Loading
            loading
            background="rgba(0, 0, 0, 0.35)"
            loaderColor="rgba(94, 147, 117, 1)"
          />
        ) : null}
      </form>
    );
  }
}

HeadTambahKas = reduxForm({
  form: "HeadTambahKas",
  enableReinitialize: true,
  validate: ValidasiUangKas,
})(HeadTambahKas);
export default connect(maptostate)(HeadTambahKas);
