import { setCamera } from "../../../actions/databarang_action";
import { React, connect, reduxForm, imgfound } from "../../../components/helpers/library";
const maptostate = (state) => {
  return {
    setCamera: state.databarang.setCamera,
  };
};
class ModalGambar extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
    };
    // this.print = this.print.bind(this);
  }
  componentWillUnmount(){
    this.props.dispatch(setCamera(false));
  }
  render() {
    return (
      <div>
        <img
          src={this.props.setCamera === false ? imgfound : this.props.setCamera}
          alt="img"
          height="245px"
          width="468px"
        />
      </div>
    );
  }
}

ModalGambar = reduxForm({
  form: "ModalGambar",
  enableReinitialize: true,
})(ModalGambar);
export default connect(maptostate)(ModalGambar);
